    async getUserlist() {
      const res = await this.$http.get(
        `/pc-mobile/examining/findPage?rows=${this.pageSize}&page=${this.currPage}&no=${this.no}&regId=${this.regId}`
      );
      console.log(res);
      this.userlist = res.data.data.list;
      this.total = res.data.data.totalCount;
    },
  },
  
};
